import chai from 'chai';
import chaiHttp from 'chai-http';
import server from '../server';
import Message from '../models/Message';
chai.use(chaiHttp);
chai.should();

/*
  * Test the /GET route
  */
 describe('/Get All messages', () => {
    it('it should GET all emails/messages', (done) => {
      chai.request(server)
          .get('/api/v1/messages')
          .end((err, res) => {
                res.should.have.status(200);
                res.body.should.be.a('array');
            done();
          });
    });
});

describe('/Get Unread messages', () => {
  it('it should GET all Unread messages', (done) => {
    chai.request(server)
        .get('/api/v1/messages/unread')
        .end((err, res) => {
              res.should.have.status(200);
              res.body.should.be.a('array');
          done();
        });
  });
});

describe('/Get Sent messages', () => {
  it('it should GET all sent messages', (done) => {
    chai.request(server)
        .get('/api/v1/messages/sent')
        .end((err, res) => {
              res.should.have.status(200);
              res.body.should.be.a('array');
          done();
        });
  });
});


// Post

describe('/POST message', () => {
  it('it should not POST a message without subject field', (done) => {
      let createmessage = {
        id:"65262e7b-0143-486d-b735-630e2fb64396",
        message:"I will came tomorrow",
        parentMessageId:"556665665",
        status:"Sent",
        createdOn:1552243675280
      }
        chai.request(server)
        .post('/api/v1/messages')
        .send(createmessage)
        .end((err, res) => {
              res.should.have.status(200);
              res.body.should.be.a('object');
              res.body.should.have.property('errors');
              res.body.errors.should.have.property('subject');
              res.body.errors.pages.should.have.property('kind').eql('required');
          done();
        });
  });
  it('it should POST a message ', (done) => {
      let createmessage = {
        id:"65262e7b-0143-486d-b735-630e2fb64396",
        subject:"Test2",
        message:"I will came tomorrow",
        parentMessageId:"556665665",
        status:"Sent",
        createdOn:1552243675280
      }
        chai.request(server)
        .post('/api/v1/messages')
        .send(createmessage)
        .end((err, res) => {
              res.should.have.status(200);
              res.body.should.be.a('object');
              res.body.should.have.property('message').eql('Message successfully sent!');
              res.body.createmessage.should.have.property('id');
              res.body.createmessage.should.have.property('subject');
              res.body.createmessage.should.have.property('message');
              res.body.createmessage.should.have.property('parentMessageId');
              res.body.createmessage.should.have.property('status');
              res.body.createmessage.should.have.property('createdOn');
          done();
        });
  });
});